export const ABOUT_HEADER = 'Hi, I’m Iuliia Kolesnikova.';

export const ABOUT_TEXT = 'Native Russian, I grew up in Moscow. I’ve been enjoying art since childhood and tried myself in a wide range of traditional arts such as watercolor painting, drawing, bead jewelry, and embroidery. My favorite artists are Alphonse Mucha, Ivan Aivazovsky, Arkhip Kuindzhi, and Claude Monet. My other passion is in IT. I hold a bachelor’s degree in Information Tecnology from the Russian State University of Trade and Economics in 2011. I had been working as an IT technical support specialist for 6 years before I moved to the USA. I recently moved to Portland, OR from Moscow, Russia, and decided to explore digital design as a profession. This new profession became a golden mean for me as the union of traditional art and digital technology.';